import React from "react";

//component
import { Col, Badge, Accordion, Button } from "react-bootstrap";
import { v4 as uuidv4 } from "uuid";
import dateFormat from "dateformat";

export default function CardsResources({
  data,
  index,
  downloadButton,
  onSubmit,
}) {
  return (
    <>
      <Col xs={12}>
        <Accordion className="w-100">
          <Accordion.Item eventKey={index}>
            <Accordion.Header>
              <div className="w-100 d-flex justify-content-between">
                {data?.content_title}
                <div className="mx-3">
                  <Badge bg="primary" pill>
                    {dateFormat(data?.created_at, "mmmm dd, yyyy")}
                  </Badge>
                </div>
              </div>
            </Accordion.Header>
            <Accordion.Body>
              <div className="py-2">
                {data?.content_tags?.map((tags) => {
                  return (
                    <Badge
                      className="pill-cursor mx-1"
                      bg="primary"
                      pill
                      key={uuidv4()}
                      onClick={() => onSubmit({ Search: tags })}
                    >
                      {tags}
                    </Badge>
                  );
                })}
              </div>
              <div
                dangerouslySetInnerHTML={{
                  __html: data?.content_description?.content_description,
                }}
              ></div>
              <Button
                variant="primary"
                className="mt-auto"
                onClick={() => downloadButton(data?.content_files)}
              >
                Download File
              </Button>
            </Accordion.Body>
          </Accordion.Item>
        </Accordion>
      </Col>
    </>
  );
}
