import React, { Fragment, useEffect, useState } from "react";

// component
import { Container, Row, Col } from "react-bootstrap";
import { isMobile } from "react-device-detect";
import { v4 as uuidv4 } from "uuid";
import { useForm } from "react-hook-form";
import { fetchData } from "./Functions/PageFunctions";
import CardsResources from "./CardsResources";
import { ScaffoldComponent } from "./Universal/Scaffold";
import { EmptySearch } from "./Universal/SearchBar";

export default function TabResources({
  category: categoryProps,
  title: titleProps,
  header: headerProps,
}) {
  const { register, handleSubmit } = useForm();
  const [dataList, setDataList] = useState([]);
  const [currentPage, setCurrentPage] = useState(0);
  const [totalPages, setTotalPages] = useState(0);
  const itemsPerPage = 10;
  const category = categoryProps;

  const setAll = (dataList, currentPage, totalPage) => {
    setDataList(dataList);
    setCurrentPage(currentPage);
    setTotalPages(totalPage);
  };

  const onSubmit = (keyword) => {
    fetchData(category, keyword.Search, currentPage, itemsPerPage).then(
      (getData) => {
        const result = getData.data.result;
        setAll(
          result,
          result.current_page,
          Math.ceil(result.total / itemsPerPage)
        );
      }
    );
  };

  const handlePageChange = (page) => {
    setCurrentPage(page);
    fetchData(category, "", page, itemsPerPage).then((getData) => {
      const result = getData.data.result;
      setAll(
        result,
        result.current_page,
        Math.ceil(result.total / itemsPerPage)
      );
    });
  };

  useEffect(() => {
    fetchData(category, "", currentPage, itemsPerPage).then((getData) => {
      const result = getData.data.result;
      setAll(
        result,
        result.current_page,
        Math.ceil(result.total / itemsPerPage)
      );
    });
  }, [category, currentPage]);

  const downloadButton = async (data) => {
    const href = process.env.REACT_APP_API_URL + "/files/contents/" + data;

    window.open(href);
  };

  const CardComponent = () => {
    return (
      <>
        <Container fluid className="m-0 p-0">
          <Row className="m-0 p-0 d-flex justify-content-center">
            <Col xs={12} lg={8}>
              <Container fluid className="m-0 p-0 w-100 mt-5 px-5">
                <Row>
                  <Col lg={12} className="h-100 align-items-center d-flex">
                    <h1 className="resources-category-card-title">
                      {titleProps}
                    </h1>
                  </Col>
                </Row>
              </Container>
              {dataList.data?.length !== 0 ? (
                <Container
                  fluid
                  className="resources-list m-0 px-lg-5 d-flex justify-content-start mb-5"
                >
                  <Row className="m-0 p-0 w-100">
                    {dataList.data?.map((data, index) => {
                      return (
                        <CardsResources
                          index={index}
                          data={data}
                          key={uuidv4()}
                          downloadButton={downloadButton}
                          onSubmit={onSubmit}
                        />
                      );
                    })}
                  </Row>
                </Container>
              ) : (
                <>
                  <Container className="vh-50">
                    <EmptySearch />
                  </Container>
                </>
              )}
            </Col>
          </Row>
        </Container>
      </>
    );
  };

  return (
    <Fragment>
      <Container fluid className="m-0 p-0 w-100 mt-5">
        <Row
          lg={12}
          className={`page-divider m-0 p-0 ${isMobile ? "w-100" : "w-75"} h-25`}
        >
          <Col lg={12} className="h-100 align-items-center d-flex">
            <h1
              className={`page-divider-text p-5 ${
                isMobile ? "text-center" : ""
              }`}
            >
              {headerProps}
            </h1>
          </Col>
        </Row>
      </Container>
      <ScaffoldComponent
        dataList={dataList}
        onSubmit={onSubmit}
        register={register}
        handleSubmit={handleSubmit}
        totalPages={totalPages}
        currentPage={currentPage}
        handlePageChange={handlePageChange}
        CardComponent={CardComponent}
      />
    </Fragment>
  );
}
