import React, { Fragment } from "react";

// component
import TabResources from "../components/TabResources";
import { Tab, Row, Nav, Col } from "react-bootstrap";
import { v4 as uuidv4 } from "uuid";

export default function PageResources() {
  const tabList = [
    {
      title: "Sermon",
      category: "sermon",
      header: "Sermon Files",
    },
    {
      title: "Newsletter",
      category: "newsletter",
      header: "Newsletter Files",
    },
    {
      title: "Files",
      category: "resources",
      header: "Other Files",
    },
  ];

  //flex-column
  return (
    <Fragment>
      <Tab.Container defaultActiveKey="sermon" transition={false}>
        <Nav
          variant="pills"
          className="d-flex justify-content-center nav-resources"
        >
          <Row>
            {tabList?.map((data) => {
              return (
                <Col key={uuidv4()}>
                  <Nav.Item>
                    <Nav.Link eventKey={data.category}>{data.title}</Nav.Link>
                  </Nav.Item>
                </Col>
              );
            })}
          </Row>
        </Nav>

        <Tab.Content>
          {tabList?.map((data) => {
            return (
              <Tab.Pane
                eventKey={data.category}
                title={data.title}
                key={uuidv4()}
              >
                <TabResources
                  category={data.category}
                  title={data.title}
                  header={data.header}
                />
              </Tab.Pane>
            );
          })}
        </Tab.Content>
      </Tab.Container>
    </Fragment>
  );
}
